<template>
  <div id="app">
    <div class="loader" v-if="isLoading">
      <b-spinner variant="white"></b-spinner>
    </div>
    <marquee class="text-white">Sayt test rejimda ishlamoqda. <span class="text-danger">Eslatma:Saytdagi kataloglar nusxalari sotuvga qo'yilgan</span></marquee>
    <router-view/>
  </div>
</template>

<script>
import {mapState} from 'vuex';

export default {
  computed: {
    ...mapState(['isLoading', 'refCount'])
  },
  created() {
    this.$axiosDefault.interceptors.request.use((config) => {
      this.$store.commit('loading', true);
      return config;
    }, (error) => {
      this.$store.commit('loading', false);
      return Promise.reject(error);
    });
    this.$axiosDefault.interceptors.response.use((response) => {
      this.$store.commit('loading', false);
      return response;
    }, (error) => {
      this.$store.commit('loading', false);
      return Promise.reject(error);
    });
  }
}
</script>

