import Vue from 'vue'
import Vuex from 'vuex'
import axios from "axios";
import router from "../router";
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isLoading:false,
    refCount:0,
    me:false,
    museums:[],
    museumsPro:[],
    organisation:[],
    museumsProSingleid:{}
  },
  mutations: {

    loading(state, isLoading) {
      console.log({isLoading})
      if (isLoading) {
        state.refCount++;
        state.isLoading = true;
      } else if (state.refCount > 0) {
        state.refCount--;
        state.isLoading = (state.refCount > 0);
      }
    },
  },

  actions: {
    getMe({state}){
      axios.get(`/customers/me/`,
          {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('reg_token')}`,
              'Accept-Language':localStorage.getItem('lang')
            }
          }
      )
          .then(res=>{
            state.me=res.data
          })
    },
      getMainOrg({state}) {
          axios.get(`organizations`,
              {
                  headers: {
                      'Accept-Language': localStorage.getItem('lang')
                  }
              })
              .then(res => {
                  state.organisation = res.data
              })
      },
    getOrgan({state}){
      const subdomain = window.location.hostname.split('.').slice(0, -2).join('.');
        if(subdomain===""){
            document.title="Yagona elektron do'kon portali"
            state.museums=[
                {
                    "title":null,
                    "file":null
                }
            ]
        }
        else {
            axios.get(`organizations?subdomain=${subdomain}`,
                {
                    headers: {
                        'Accept-Language':localStorage.getItem('lang')
                    }
                })
                .then(res=>{
                    state.museums=res.data
                    document.title=state.museums[0].title
                })
        }
    },
      getOrganPro({state}){
          const subdomain = window.location.hostname.split('.').slice(0, -2).join('.');
         if(subdomain===""||subdomain==="shop"){
             axios.get(`new-products/`,
                 {
                     headers: {
                         'Accept-Language':localStorage.getItem('lang')
                     }
                 })
                 .then(res=>{
                     state.museumsPro=res.data
                 })
         }
         else {
             axios.get(`organizations/${subdomain}/new-products/`,
                 {
                     headers: {
                         'Accept-Language':localStorage.getItem('lang')
                     }
                 })
                 .then(res=>{
                     state.museumsPro=res.data
                 })
         }
      },
      getOrganProSingle({state}){
          console.log(router.currentRoute.params.id,"jdsandjksa")
          const subdomain = window.location.hostname.split('.').slice(0, -2).join('.');
          if(subdomain===""){
              axios.get(`products/${router.currentRoute.params.id}`,
                  {
                      headers: {
                          'Accept-Language':localStorage.getItem('lang')
                      }
                  })
                  .then(res=>{
                      state.museumsProSingleid=res.data
                  })
          }
          else {
              axios.get(`organizations/${subdomain}/products/${router.currentRoute.params.id}`,
                  {
                      headers: {
                          'Accept-Language':localStorage.getItem('lang')
                      }
                  })
                  .then(res=>{
                      state.museumsProSingleid=res.data
                  })
          }
      },

  },
  modules: {
  }
})
